export default {
  props: {
    boss: {
      type: Boolean,
      default: false,
    },
    value: {
      type: Number,
      required: true,
    },
    background: {
      type: String,
      default: "#fbf3e7",
    },
  },
  computed: {
    index() {
      return Math.ceil(100 / this.value);
    },
  },
};
