import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Boss',
    component: () => import('../views/boss/index.vue')
  },
  {
    path: '/operate',
    name: 'Operate',
    component: () => import('../views/operate/index.vue')
  }
]

const router = new VueRouter({
  routes
})

export default router
