export default {
  props: {
    show: {
      type: Boolean,
      required: true,
    },
    value: {
      type: String,
    },
  },
  data() {
    return {
      columns: ["成都", "宁波", "杭州", "温州", "绍兴", "湖州", "嘉兴", "金华"],
    };
  },
  watch: {
    // show(cur) {
    //   console.log(cur);
    //   if(cur) {
    //     this.$refs.picker && this.$refs.picker.setValues([this.value])
    //   }
    // }
  },
  methods: {
    onConfirm(e) {
      this.$emit("confirm", e);
    },
    onCancel(e) {
      this.$emit("update:show", false);
    },
  },
};
